.app {
  background: #e4f5ff;
  padding: 30px;
  height: 100vh;
  align-content: center;
}

.topack-list {
  background: #96c7d5;
  border-radius: 4px;
  padding: 5px;
  max-width: 400px;
  align-items: center;
  align-self: center;
  align-content: center;
}

.heading{
  border-radius: 4px;
  text-align: center;
  padding: 5px;
  max-width: 400px;
  align-content: center;
  left: 10px;
  color: #022534;
}


.topack {
  background: #fff;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15);
  padding: 3px 10px;
  font-size: 12px;
  margin-bottom: 6px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-self: center;
}